












































import { Component, Vue } from "vue-property-decorator";
import { namespace } from "vuex-class";
import Category from "../models/Category";
import NavBar from "@/components/navbar/NavBar.vue";

const categories = namespace("Categories");
const expenses = namespace("Expenses");

@Component({
  components: {
    NavBar
  }
})
export default class Categories extends Vue {
  private type = 1;
  private types = {
    1: "Income",
    2: "Outcome"
  };

  mounted() {
    this.checkLengths();
  }

  @expenses.Getter
  public sortedById!: Array<Category>;

  @categories.Getter
  public incomes!: Array<Category>;

  @categories.Getter
  public outcomes!: Array<Category>;

  @categories.Action
  deleteCategory!: (id: number) => void;

  get categories(): Array<Category> {
    if (this.type === 1) {
      return this.incomes;
    }
    return this.outcomes;
  }

  get typeSwitcherEnabled(): boolean {
    return this.incomes.length > 0 && this.outcomes.length > 0;
  }

  public changeType(): void {
    if (this.type === 1) {
      this.type = 2;
      return;
    }
    this.type = 1;
  }

  public deleteHandler(id: number): void {
    this.deleteCategory(id);
    this.checkLengths();
  }

  private checkLengths(): void {
    if (this.incomes.length === 0) {
      this.type = 2;
    }

    if (this.outcomes.length === 0) {
      this.type = 1;
    }
  }
}
